body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background: linear-gradient(135deg, #f54ea2, #ff7676);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.app {
  background: rgba(0, 0, 0, 0.7);
  padding: 20px 30px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  width: 80%;
  max-width: 500px;
}

.question-section {
  margin-bottom: 20px;
}

.question-count {
  font-size: 18px;
  margin-bottom: 10px;
}

.question-text {
  font-size: 22px;
  font-weight: bold;
}

.answer-section button {
  margin: 10px 0;
  padding: 12px 20px;
  font-size: 18px;
  cursor: pointer;
  border: none;
  background-color: #ff7676;
  color: white;
  border-radius: 8px;
  width: 100%;
  transition: transform 0.2s, background-color 0.3s;
}

.answer-section button:hover {
  background-color: #f54ea2;
  transform: scale(1.05);
}

.score-section {
  font-size: 24px;
  font-weight: bold;
  color: #ffca28;
  text-align: center;
}
